import { Injectable } from '@angular/core';
import { Http, Response } from "@angular/http";

@Injectable()
export class CarService {
  private labels: string[];

  constructor(private http: Http) {
    this.labels = ["ToyotaVitz", "DaihatsuMove", "DaihatsuTanto", "SuzukiWagonR", "MazdaCX-5", "ToyotaAqua", "SubaruForester", "ToyotaRAV4", "HondaFit", "MazdaAxela", "ToyotaLandCruiser", "ToyotaPrius"];
  }

  accessPredictApi(fileToUpload: any, apiUrl: string) {
    let input = new FormData();
    input.append("image", fileToUpload);
    let response =  this.http.post(apiUrl, input);
    let results
    return response.map((res:Response) => {
      let scores = res.json().scores;
      let results = [];

      // resultsnamescore
      this.labels.forEach((item, index) => {
        results[index] = {
          rank: 0,
          name: item,
          value: parseFloat(scores[index])
        }
      });

      // tempArrayscores0-11
      let tmpArray = [];
      scores.forEach((value, index) => {
        tmpArray[index] = {
          index: index,
          value: parseFloat(value)
        }
      });

      // rankArrayscoretmpArrayindex
      let rankArray = tmpArray.sort(function(a,b){return(-a.value + b.value);});

      // results
      for ( let i = 0; i < rankArray.length; i++ ) {
        results[rankArray[i].index].rank = i + 1;
      }
      results = results.sort(function(a,b){return(a.rank - b.rank);});

      return results;
    });
  }

}



