import {Component} from '@angular/core';
import { CarService } from './car.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'car',
  template: require('./car.html'),
  providers: [CarService],
  styles: [
    require('./car.css'),
    require('../common/vendor-sb-admin-2/metisMenu/metisMenu.min.css'),
    require('../common/dist-sb-admin-2/css/sb-admin-2.css'),
    require('../common/vendor-sb-admin-2/morrisjs/morris.css'),
    require('../common/style-sb-admin-2.css'),
  ]
})


export class Car {
  public files: any[];
  public kind: string[] = ["ToyotaVitz", "DaihatsuMove", "DaihatsuTanto", "SuzukiWagonR", "MazdaCX-5", "ToyotaAqua", "SubaruForester", "ToyotaRAV4", "HondaFit", "MazdaAxela", "ToyotaLandCruiser", "ToyotaPrius"];
  public results: any[] = [{ rank: '-', name: '', value: 0}];
  public apiUrl: string = "http://54.191.65.203/predict";
  public imageFileSrc: string;
  public testVariable : string;


  constructor(private CarService: CarService) {
    this.kind.map((name, index) => {
      this.results[index] = { rank: '-', name: name }
    });
  }

  onChange(event) {
    this.results = [];
    this.retrieveResult(event.srcElement.files);
    this.showImage(event.srcElement.files[0]);
  }

  retrieveResult(files) {
    if (files[0]) {
        console.log(files[0]);
        let results$ = this.CarService.accessPredictApi(files[0], this.apiUrl);
        results$.subscribe(v => {
          console.log(v);
          this.results = v;
        });
    }
  }

  showImage(imageFile) {
    let fileRdr:FileReader = new FileReader();
    fileRdr.onloadend = this.imageOnloadedCallback;
    let response = fileRdr.readAsDataURL(imageFile);
    // console.log("response");
    // console.log(response);
    return response;
  }

  //  readFile(file, onLoadCallback){
  //   var reader = new FileReader();
  //   reader.onload = onLoadCallback;
  //   reader.readAsText(file);
  // }

  // private sanitizer: DomSanitizer;
  imageOnloadedCallback(event:any) {
    // this.testVariable = "testest";
    // this.imageFileSrc = this.sanitizer.bypassSecurityTrustUrl(event.target.result);

    // 
    var preview = document.querySelector('img');
    preview.src = event.target.result;

    // return "a";
    // console.log(this.imageFileSrc);
    // this.imageFileSrc = "start";
    // console.log(this.imageFileSrc);
    // this.imageFileSrc = "end";
  //   console.log(this.imageFileSrc);
  //   console.log(this.imageFileSrc);
    // return event.target.result;
  }

}



